.spinner {
  width: 120px;
  height: 120px;
  border: 16px solid hsl(var(--color-black) / 10%);
  border-top: 16px solid hsl(var(--color-primary));
  margin: 20px auto;
  animation: spin 2s linear infinite;
  border-radius: 50%;
}

.spinner__text {
  position: absolute !important;
  overflow: hidden !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  border: 0 !important;
  margin: -1px !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
